import { Component, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController, Loading, Platform } from 'ionic-angular';
import { ToolsProvider } from '../../providers/tools/tools';
import { DomSanitizer } from '@angular/platform-browser';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { CommonServiceProvider } from '../../providers/common-service/common-service';
import { ServiceForm } from '../../model/service-form-model';
import { RoommakeupServiceProvider } from '../../providers/roommakeup-service/roommakeup-service';
import { NotificationProvider } from '../../providers/notification/notification';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

//Xevi
import { ToastController } from 'ionic-angular';

/**
 * Generated class for the RestaurantBookingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-roommakeupdetails',
  templateUrl: 'room-makeup-details.html',
})
export class RoommakeupDetailsPage {
  makeupForm: FormGroup;
  loading: Loading;
  item: any = [];
  type: any;
  res: any; //to store return data while language translate
  descriptionHtml: any;
  backgroundImage: any; // to store background image url
  suffix: any;
  personArray: any = []; // to store person for select option
  dateType: string; // date type
  start: number;
  end: number;
  timeSlots: any = [];
  turnList: any = []; //turn list
  monthNames: any; // for month names
  monthShortNames: any = []; // for month short names
  minDate: any = new Date();
  maxDate: any;
  dayShortNames: any = [];
  firstSuffix: any;
  platFormIsIOS: boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, public formBuilder: FormBuilder,
    public toolsPvdr: ToolsProvider, public sanitized: DomSanitizer, public commonProvider: CommonServiceProvider,
    public roommakeupProvider: RoommakeupServiceProvider, public alertCtrl: AlertController,
    private toastCtrl: ToastController,
    public cdRef: ChangeDetectorRef, private notificationProvider: NotificationProvider, private loadingCtrl: LoadingController, private platForm: Platform) {

    this.type = this.navParams.get('itemId');
    //this.item = this.navParams.get('item'); // gets item
    if (this.type == 1) {
      this.descriptionHtml = 'Selecione hasta que hora no desea ser molestado y agregue los comentarios que considere oportunos. Nuestro staff se encargará de gestionarlo.';
      this.backgroundImage = 'assets/imgs/housekeeping/nodisturb_background.jpg';
    }else{
      this.descriptionHtml = 'Agregue los comentarios que considere oportunos. Nuestro staff se encargará de gestionarlo.';
      this.backgroundImage = 'assets/imgs/housekeeping/room_makeup_background.jpg';
    }

    this.makeupForm = this.formBuilder.group({
      'specialRequests': new FormControl(''),
      'time': new FormControl('', [Validators.required]),
    });
  }

  ionViewDidLoad() {
    var hour = new Date().getHours();
    var minute = new Date().getMinutes();
    if (minute >= 30) {
      hour = hour + 1;
      minute = 0;
    }else{
      minute = 30;
    }
    //this.fndebug(hour + ":" + minute);
    this.timeSlots = this.commonProvider.fnConvertToTimeSlotsArray(hour + ":" + minute, "23:30", 30);
    //this.fndebug(this.timeSlots);
  }

  // store entered data into model before leaving page
  public ionViewWillLeave() {
  }

  //function to go to previous page
  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  public fnSubmit() {
    if (this.makeupForm.valid){
      //Dont Disturb
      if (this.type == 1) {
        //Enviem la petició i esperem resposta....
        this.roommakeupProvider.sendDontDisturbData('', this.makeupForm.get('time').value, this.makeupForm.get('specialRequests').value,202,122332)
          .subscribe(
            (data) => {
              this.fndebug(data);
              let alertnew = this.alertCtrl.create({
                title: 'ALERT',
                message: this.makeupForm.status,
                cssClass: 'cls_alrt',
                buttons: [
                  {
                    text: 'OK',
                    role: 'cancel'
                  }]
              });
              alertnew.present();
              setTimeout(() => { alertnew.dismiss(); }, 5000);
            }
          );

      }
    }
  }

  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }

  //Xevi
  private fndebug(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.onDidDismiss(() => {
    });

    toast.present();
  }


}



